.Footer {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 20vh;
  background-color: #080613;
  border-color: transparent !important;
  animation: FadeIn 1s forwards;
}

.scLink {
  color: #f246ff;
  opacity: 1 !important;
}


.Clicker {
  display: flex;
  align-items: center;
  width: 50%;
  margin-left: 32px;
  margin-top: 64px;

  .ClickerButton {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 60px;
    width: 60px;
    border: transparent;
    background-color: transparent !important;
    transition: .5s;

    .ButtonIcon {
      margin-right: 15px;
      color: white;
      transform: scale(1.8);
      transition: .5s;
    }
  }

  .ClickerButton:hover {

    rotate: 45deg;
    transition: .4s;
    cursor: pointer;

    .ButtonIcon {
      transform: scale(2);
      transition: .5s;
    }
  }

  .ClickerButton:active {
    cursor: pointer;
    rotate: 54deg;
    transition: .1s;

    .ButtonIcon {
      margin-bottom: 10px;
      color: #FF00E5;
      transform: scale(3);
      transition: .1s;
    }
  }

  .Counter {
    position: absolute;
    transform: translate(23px, 35px);
    width: 20px !important;
    color: white;
    font-size: 14px;
    font-family: "Space Grotesk", sans-serif;
    font-weight: bold;
    opacity: 0;
    transition: .5s;
  }

  .Counter.active {
    opacity: 100%;
    color: #FF00E5;
    transition: .1s;
  }
}

.Contacts {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-end;
  font-weight: bold;
  width: 50%;
  margin-right: 128px;
  color: white !important;

  .ContactItems {
    display: flex;
    flex-direction: row;
    height: 20px;
    margin-top: 18px;
    font-weight: lighter;
    opacity: 80%;

    .ItemIcon {
      margin-top: 15px;
      margin-right: 20px;
      opacity: 100%;
    }
  }
}
.Contacts::before {
  content: '';
  transform: translate(0px, 30px);
  background-color: #FF00E5;
  height: 2px;
  box-sizing: border-box;
  width: 50px;
}

.Copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  margin-top: 6%;
  opacity: 50%;
  color: white !important;
  transition: .4s;

  &:hover {
    cursor: pointer;
    opacity: 80%;
    transform: translateY(-5px);
    transition: 0.3s;
  }
}

@media (max-width: 450px) {
  .Footer {
    display: none;
  }
}


@media (max-height: 800px) {

  .Footer {
    height: auto;
    min-height: 200px !important;
  }
}

@keyframes FadeIn {
  0% {
    opacity: 0.9;
  }

  100% {
    opacity: 1;
  }
}