.MegaDiv {
  display: block;
  position: fixed;
  pointer-events: none;
  transition: 850ms;
  z-index: 100;
}

.MegaDiv.active {
  display: block !important;
  transition: 850ms;
  pointer-events: auto;
}

.Transition {
  transition: 850ms;
}

.SideBase {
  background-color: aliceblue;
  flex-direction: column;
}

.navbar {
  background-color: #b55eff;
  height: 80px;
  width: 80px;
  cursor: pointer;
  z-index: 100 !important;
  position: fixed;
  top: 40%;
  border-radius: 50%;
  display: flex;
  justify-content: start;
  align-items: center;
  transform: translateX(-40%) !important;
  transition: 850ms;
}

.navbar.active {
  background-color: white;
  transform: translateX(200%) !important;
  transition: 850ms;
}


.menu-bars {
  margin-left: 12px;
  margin-bottom: 20px;
  font-size: 2rem;
  background: none;
  transition: top 0.4s;

  &:hover {
    transition: 0.4s;
  }
}

a {
  text-decoration: none;
  color: #200C36;
  transition: 0.4s;

}

.nav-menu {
  background-color: #180c38;
  width: 200px;
  height: 100vh;
  display: flex;
  justify-content: center;
  top: 0;
  transform: translateX(-100%) !important;
  transition: 850ms;
}

.nav-menu.active {
  transform: translateX(0%) !important;
  transition: 850ms;
  z-index: 100 !important;
}

.nav-text {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0 8px 0;
  margin-right: 60px;
  list-style: none;
  height: 60px;

  Button {
    text-decoration: none;
    color: white;
    font-size: 18px;
    width: 300px;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: start;
    border-radius: 4px;

    &:hover {
      background-color: aqua;
    }
  }
}

.nav-menu-items {
  width: 100%;
}

.navbar-toggle {
  background-color: #200C36;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

span {
  margin-left: 16px;
}

@media (max-width: 450px) {
  .MegaDiv {
    display: none !important;
  }
  .MegaDiv.active {
    display: none !important;
  }
  .navbar {
    display: none;
  }
  .navbar.active {
    display: none;
  }
}

